import Vue from 'vue'
import Router from 'vue-router'

import luckyWheel from './views/luckyWheel'
import wheelRecord from './views/wheelRecord'


Vue.use(Router);

export default new Router({
    routes: [
        { path: '/',
            redirect: '/luckyWheel'
        },
        {
            path: '/luckyWheel',
            name: 'luckyWheel',
            component: luckyWheel
        },
        {
            path: '/wheelRecord',
            name: 'wheelRecord',
            component: wheelRecord
        },
    ]
})
