<template>
    <div class="wheelRecordWrap">
        <div class="title">中奖纪录</div>
        <div class="recordWrap">
            <div class="recordTitle">
                <span>中奖时间</span>
                <span>所中奖品</span>
            </div>
            <ul>
                <li v-for="item in entities">
                    <span>{{item.create_time}}</span>
                    <span>{{item.prize_type}}</span>
                </li>
                <li class="noMore" v-if="entities.length === 0">暂时没有更多中奖纪录哦！</li>
            </ul>

        </div>
    </div>
</template>

<script>
    let qs = require('qs');

    export default {
        name: 'wheelRecord',
        data: function () {
            return {
                api:"/api",
                userId:"",
                entities:[],
            }
        },
        created: function () {
            var _this = this;
            this.userId = this.$route.query.userId;
            console.log(this.$store.state.api);
            if(this.$store.state.api){
                this.api = this.$store.state.api;
            }

            var myDate = new Date();
            var year = myDate.getFullYear();
            var month = myDate.getMonth() + 1;
            if(month < 10){
                month = "0" + month;
            }
            var day = myDate.getDate();
            if(day < 10){
                day = "0" + day;
            }

            this.$http.post('https://commonapi.matrixpr.net/commonapi/prize/get',qs.stringify({userid: _this.userId}))
                .then(function (res) {
                    console.log(res.data);
                    if(res.data.mData && res.data.mData.entities){
                        _this.entities = res.data.mData.entities;
                    }
                })
                .catch(function (error) {
                    console.log(error);
                });

        },
        mounted: function () {

        },
        methods: {

        }
    }
</script>

<style scoped>
    .wheelRecordWrap{
        height:100vh;
        background:rgb(1,157,231);
    }
    .title{
        width:84vw;
        height:34px;
        line-height:34px;
        background:url(../../public/images/titleBg.png) no-repeat 0 0/100% 100%;
        margin:0 auto;
        position:relative;
        top:20px;
        font-size:20px;
        font-weight:bold;
        color:rgb(1,157,231);
    }
    .recordWrap{
        width:84vw;
        height:86vh;
        margin:0 auto;
        position:relative;
        top:42px;
        background:url(../../public/images/recordBg.png) no-repeat 0 0/100% 100%;
        padding:0 10.6vw;
        padding-top:3vh;
    }
    .recordWrap ul{
        height:70vh;
        overflow-y: auto;
    }
    .recordWrap li{
        height:42px;
        line-height:42px;
        width:100%;
        margin:0 auto;
        border-top:1px solid #D0D6E9;
    }
    .recordWrap li:last-child{
        border-bottom:1px solid #D0D6E9;
    }
    .recordTitle{
        height:44px;
        line-height:44px;
        padding-left:2.67vw;
        padding-right:2.67vw;
        /*border-bottom:1px solid #D0D6E9;*/
    }
    .recordTitle:after{
        clear:both;
        content:"";
        display:block;
        height:0;
    }
    .noMore{
        border:none;
        color: #019EE8;
    }
    .recordTitle span{
        font-size:16px;
        font-weight: bold;
    }
    .recordWrap li:after{
        clear:both;
        content:"";
        display:block;
        height:0;
    }
    .recordWrap li span:nth-child(1),.recordTitle span:nth-child(1){
        float:left;
        color: #019EE8;
    }
    .recordWrap li span:nth-child(2),.recordTitle span:nth-child(2){
        float:right;
        color: #E34C84;
    }
</style>
