<template>
    <div class="wrap">
        <div class="rewardWrap" v-if="rewardMaskShow" @click="rewardMaskShow = false">
            <p class="animated bounceInDown">恭喜您抽中特等奖！</p>
            <img :class="{zoomIn:isZoomIn,tada:isTada}" class="reward animated" src="../../public/images/reward.png" alt="">
        </div>
        <div class="tipMsg" v-if="tipMsg">{{tipMsg}}</div>
        <img class="lightBg" src="../../public/images/lightBg.png" alt="">
        <img class="topBg" src="../../public/images/topBg.png" alt="">
        <img class="logo" src="../../public/images/logo.png" alt="">
        <!--<div class="ruleWrap">-->
            <!--<span></span>-->
            <!--查看规则-->
        <!--</div>-->
        <img class="title" src="../../public/images/title.png" alt="">
        <div class="wheelWrap">
            <ul class="wheel">
                <li v-for="item in goodsList">
                    <div>
                        <span>{{ item.name }}</span>
                        <!--<span :style='"background:url(" + item.pic + ") no-repeat 0 0/100% 100%;"'></span>-->
                        <span></span>
                    </div>
                </li>
            </ul>
        </div>
        <div class="startDraw" @click="startDraw()"></div>
        <div class="btn1 btnYellow">
            当前积分：{{score}}
        </div>
        <div class="btn1 btnBlue" @click="toRecord()">
            查看中奖纪录
        </div>
        <img class="bottomBg" src="../../public/images/bottomBg.png" alt="">
    </div>
</template>

<script>
    let qs = require('qs');

    export default {
        name: 'luckyWheel',
        data: function () {
            return {
                api:"/api",
                score:"0",
                userId:"",
                goodsList:[
                    {
                        pic:"../../public/images/awards01.png",
                        name:"特等奖"
                    },
                    {
                        pic:"../../public/images/awards01.png",
                        name:"谢谢参与"
                    },
                    {
                        pic:"../../public/images/awards01.png",
                        name:"40积分"
                    },
                    {
                        pic:"../../public/images/awards01.png",
                        name:"16积分"
                    },
                    {
                        pic:"../../public/images/awards01.png",
                        name:"8积分"
                    },
                    {
                        pic:"../../public/images/awards01.png",
                        name:"4积分"
                    },
                ],
                isCanDraw:true,
                angles:null,
                tipMsg:"",
                isRotate:false,
                rewardMaskShow:false,
                isZoomIn:false,
                isTada:false
            }
        },
        created: function () {
            var _this = this;
            this.userId = this.$route.query.userId;
            console.log(this.$store.state.api);
            if(this.$store.state.api){
                this.api = this.$store.state.api;
            }

            var myDate = new Date();
            var year = myDate.getFullYear();
            var month = myDate.getMonth() + 1;
            if(month < 10){
                month = "0" + month;
            }
            var day = myDate.getDate();
            if(day < 10){
                day = "0" + day;
            }

            $(".wheel").addClass("rotate");
            var arr = [0,60,120,180,240,300];
            // this.angles = arr[parseInt(Math.random()*8)];
            // this.angles = 60;
            this.getScore();


        },
        mounted: function () {

        },
        methods: {
            toRecord:function(){
                var _this = this;
                if(!this.isRotate){
                    this.$router.push({
                        path: '/wheelRecord',
                        query:{
                            userId:_this.userId
                        }
                    })
                }else{
                    _this.tipMsg = "抽奖结束才能查看中奖纪录哦！";
                    setTimeout(function(){
                        _this.tipMsg = "";
                    },2000);
                }
            },
            startDraw:function(limit){
                var _this = this;
                if(this.isCanDraw){
                    // this.$http.post('/java2/commonapi/prize/create',qs.stringify({userid: _this.userId}))
                    this.$http.post('https://commonapi.matrixpr.net/commonapi/prize/create',qs.stringify({userid: _this.userId}))
                        .then(function (res) {
                            console.log(res);
                            console.log(parseInt(res.data.return_code));
                            if(res.data.mData){
                                _this.isCanDraw = false;
                                $('.wheelWrap').css({'transform':'rotate(0)',"transition":"0s linear"});
                                _this.isRotate = true;
                                _this.angles = parseInt(res.data.mData.prize_angle);
                                _this.angles += 720 * 5;
                                setTimeout(function(){
                                    _this.rotate($('.wheelWrap'),_this.angles);
                                },100);

                                setTimeout(function(){
                                    _this.isCanDraw = true;
                                    _this.isRotate = false;
                                    _this.getScore();
                                    console.log(res.data.mData.prize_angle);
                                    switch (parseInt(res.data.mData.prize_angle)) {
                                        case 0:
                                            _this.rewardMaskShow = true;
                                            // $(".reward").addClass("zoomIn");
                                            _this.isZoomIn = true;
                                            setTimeout(function(){
                                                _this.isZoomIn = false;
                                                _this.isTada = true;
                                            },1000);

                                            break;
                                        case 60:
                                            _this.showTipMsg("恭喜您获得4积分！");
                                            break;
                                        case 120:
                                            _this.showTipMsg("恭喜您获得8积分！");
                                            break;
                                        case 180:
                                            _this.showTipMsg("恭喜您获得16积分！");
                                            break;
                                        case 240:
                                            _this.showTipMsg("恭喜您获得40积分！");
                                            break;
                                        case 300:
                                            _this.showTipMsg("谢谢参与哦！");
                                            break;
                                    }
                                },3100);
                            }else{
                                _this.tipMsg = "每天只能抽奖一次哦！";
                                setTimeout(function(){
                                    _this.tipMsg = "";
                                },2000);
                            }


                        })
                        .catch(function (error) {
                            console.log(error);
                        });
                }
            },
            showTipMsg:function(msg){
                var _this = this;
                _this.tipMsg = msg;
                setTimeout(function(){
                    _this.tipMsg = "";
                },2000);
            },
            getScore:function(){
                var _this = this;
                this.$http.post('https://commonapi.matrixpr.net/commonapi/prize/get',qs.stringify({userid: _this.userId}))
                    .then(function (res) {
                        console.log(res.data.mData.score);
                        if(res.data.mData && res.data.mData.score){
                            _this.score = res.data.mData.score;
                        }else{
                            _this.score = 0;
                        }

                    })
                    .catch(function (error) {
                        console.log(error);
                    });
            },
            rotate:function($obj,angles) {
                // $obj.css('-o-transform', 'rotate('+angles+'deg)'); //Opera
                // $obj.css('-ms-transform', 'rotate('+angles+'deg)'); //IE浏览器
                // $obj.css('-moz-transform', 'rotate('+angles+'deg)');//Firefox
                // $obj.css('-webkit-transform', 'rotate('+angles+'deg)'); //Chrome和Safari
                $obj.css({'transform':'rotate('+angles+'deg)',"transition":"3s linear"});
            }
        }
    }
</script>

