        top:46vw;
        background:url(../../public/images/wheel.png) no-repeat 0 0/100% 100%;
    }
    /*.wheelWrap{*/
        /*width:100%;*/
        /*height:100%;*/
        /**/
    /*}*/
    .wheel{
        width:100%;
        height:100%;
    }
    .wheel li {
        width: 36vw;
        height: 39.6vw;
        display: block;
        background: rgba(0, 0, 0, 0);
        top: 0;
        position:absolute;
