        background:url(../../public/images/award01.png) no-repeat 0 0/100% 100%;
    }
    .wheel li:nth-child(2) div span:nth-child(2) {
        background:url(../../public/images/award02.png) no-repeat 0 0/100% 100%;
        transform: rotateZ(-90deg);
    }
    .wheel li:nth-child(3) div span:nth-child(2) {
        background:url(../../public/images/award03.png) no-repeat 0 0/100% 100%;
    }
    .wheel li:nth-child(4) div span:nth-child(2) {
        background:url(../../public/images/award03.png) no-repeat 0 0/100% 100%;
    }
    .wheel li:nth-child(5) div span:nth-child(2) {
        background:url(../../public/images/award03.png) no-repeat 0 0/100% 100%;
    }
    .wheel li:nth-child(6) div span:nth-child(2) {
        background:url(../../public/images/award03.png) no-repeat 0 0/100% 100%;
    }

    .wheel li:nth-child(2) {
        transform: rotateZ(60deg);
        transform-origin: bottom center;
    }
    .wheel li:nth-child(3) {
        transform: rotateZ(120deg);
