    .btnYellow{
        left:1.07vw;
        background:url(../../public/images/btnYellow.png) no-repeat 0 0/100% 100%;
        color:#16318F;
    }
    .btnBlue{
        right:4vw;
        background:url(../../public/images/btnBlue.png) no-repeat 0 0/100% 100%;
